/* Modern NiyuktiPath - Professional Job Portal */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  /* Modern color palette */
  --primary: #2563eb;
  --primary-hover: #1d4ed8;
  --secondary: #1e40af;
  --accent: #3b82f6;
  --accent-light: #93c5fd;
  --dark: #0f172a;
  --dark-2: #1e293b;
  --light: #f8fafc;
  --light-2: #f1f5f9;
  --gray: #64748b;
  --gray-light: #e2e8f0;
  --success: #10b981;
  --error: #ef4444;
  
  /* Spacing */
  --space-xs: 0.5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  
  /* Typography */
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-lg: 1.125rem;
  --text-xl: 1.25rem;
  --text-2xl: 1.5rem;
  --text-3xl: 1.875rem;
  --text-4xl: 2.25rem;
  --text-5xl: 3rem;
  
  /* Border radius */
  --radius-sm: 0.5rem;
  --radius-md: 0.75rem;
  --radius-lg: 1rem;
  --radius-xl: 1.5rem;
  --radius-full: 9999px;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-lg: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  /* Transitions */
  --transition: all 0.2s ease-in-out;
  --transition-slow: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  color: var(--dark);
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 var(--space-md);
}

.section {
  padding: var(--space-xl) 0;
  position: relative;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: var(--space-sm);
  color: var(--dark);
}

p {
  color: var(--gray);
  margin-bottom: var(--space-sm);
}

a {
  color: var(--primary);
  text-decoration: none;
  transition: var(--transition);
}

a:hover {
  color: var(--primary-hover);
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  font-size: var(--text-base);
  line-height: 1.5;
  cursor: pointer;
  transition: var(--transition);
  border: none;
  outline: none;
  text-align: center;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  position: relative;
  overflow: hidden;
}

.btn:focus {
  outline: 2px solid var(--accent-light);
  outline-offset: 2px;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.primary-btn {
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  color: white;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.2);
}

.primary-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(37, 99, 235, 0.3);
}

.primary-btn:active:not(:disabled) {
  transform: translateY(0);
}

.outline-btn {
  background: transparent;
  border: 1px solid var(--gray-light);
  color: var(--dark);
  background-color: white;
}

.outline-btn:hover:not(:disabled) {
  background-color: var(--light-2);
  border-color: var(--gray-light);
  transform: translateY(-2px);
}

.outline-btn:active:not(:disabled) {
  transform: translateY(0);
}

/* Card styles */
.card {
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow);
  overflow: hidden;
  transition: var(--transition);
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}

/* Utility classes */
.text-center { text-align: center; }
.mt-1 { margin-top: var(--space-xs); }
.mt-2 { margin-top: var(--space-sm); }
.mt-4 { margin-top: var(--space-md); }
.mt-6 { margin-top: var(--space-lg); }
.mt-8 { margin-top: var(--space-xl); }

.py-8 { padding-top: var(--space-xl); padding-bottom: var(--space-xl); }
.px-4 { padding-left: var(--space-md); padding-right: var(--space-md); }

.relative { position: relative; }
.z-10 { z-index: 10; }

/* ===== HERO SECTION ===== */
.home-hero {
  position: relative;
  padding: 6rem 0;
  background: linear-gradient(135deg, #f8fafc 0%, #f0f9ff 100%);
  overflow: hidden;
}

.home-hero::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -10%;
  width: 80%;
  height: 200%;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.08) 0%, rgba(59, 130, 246, 0) 70%);
  z-index: 0;
}

.hero-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

/* Hero Content */
.hero-left {
  max-width: 600px;
}

.hero-left .hero-title {
  font-size: var(--text-5xl);
  font-weight: 800;
  line-height: 1.1;
  color: var(--dark);
  margin-bottom: 1.5rem;
  background: linear-gradient(90deg, var(--dark), var(--primary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.hero-left .hero-title .accent {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.hero-left .hero-sub {
  font-size: 1.25rem;
  line-height: 1.6;
  color: var(--gray);
  margin-bottom: 2rem;
  max-width: 90%;
}

.hero-ctas {
  display: flex;
  gap: 1rem;
  margin-bottom: 2.5rem;
}

.hero-stats {
  display: flex;
  gap: 2.5rem;
  margin-top: 2.5rem;
  padding-top: 2rem;
  border-top: 1px solid var(--gray-light);
}

.hero-stats .stat {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding-left: 1.5rem;
}

.hero-stats .stat:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -1.25rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 40%;
  background-color: var(--gray-light);
}

.hero-stats .stat strong {
  display: block;
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--primary);
  line-height: 1.2;
  margin-bottom: 0.25rem;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.hero-stats .stat span {
  font-size: 0.875rem;
  color: var(--gray);
  font-weight: 500;
}

/* ===== HERO IMAGE/PHONE MOCKUP ===== */
.phone-stage {
  position: relative;
  width: 100%;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 1000px;
}

.phone-float {
  width: 300px;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  background: linear-gradient(145deg, #ffffff, #f0f4ff);
  box-shadow: 0 25px 50px -12px rgba(37, 99, 235, 0.15), 
              20px 20px 60px rgba(37, 99, 235, 0.1),
              -20px -20px 60px rgba(255, 255, 255, 0.8);
  overflow: hidden;
  position: relative;
  z-index: 20;
  transform-style: preserve-3d;
  transition: transform 0.5s cubic-bezier(0.16, 1, 0.3, 1);
  will-change: transform;
  border: 1px solid rgba(255, 255, 255, 0.8);
}

.phone-float::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 12px;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  z-index: 2;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
}

.phone-mockup {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 32px;
}

.phone-mockup img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 28px;
}

/* Floating feature cards */
.feature-card {
  position: absolute;
  width: 240px;
  background: white;
  border-radius: var(--radius-lg);
  padding: 1.25rem;
  box-shadow: var(--shadow-lg);
  z-index: 10;
  transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.8);
}

.feature-card h4 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: var(--primary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.feature-card h4::before {
  content: 'âœ“';
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  background: rgba(37, 99, 235, 0.1);
  border-radius: 50%;
  font-size: 0.75rem;
  color: var(--primary);
}

.feature-card p {
  font-size: 0.875rem;
  color: var(--gray);
  line-height: 1.5;
  margin: 0;
}

/* Card positions with smooth animations */
.feature-card.left.top {
  left: -2rem;
  top: 3rem;
  transform: translateX(-1rem) rotate(-3deg);
  animation: float 6s ease-in-out infinite;
}

.feature-card.left.bottom {
  left: -2.5rem;
  bottom: 3rem;
  transform: translateX(-1rem) rotate(-1deg);
  animation: float 7s ease-in-out 0.5s infinite;
}

.feature-card.right.top {
  right: -2rem;
  top: 4rem;
  transform: translateX(1rem) rotate(3deg);
  animation: float 6.5s ease-in-out 0.3s infinite;
}

.feature-card.right.bottom {
  right: -2.5rem;
  bottom: 4rem;
  transform: translateX(1rem) rotate(2deg);
  animation: float 7.5s ease-in-out 0.7s infinite;
}

/* Floating animation */
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(var(--rotate, 0deg));
  }
  50% {
    transform: translateY(-10px) rotate(var(--rotate, 0deg));
  }
}

/* Add specific rotation for each card */
.feature-card.left.top { --rotate: -3deg; }
.feature-card.left.bottom { --rotate: -1deg; }
.feature-card.right.top { --rotate: 3deg; }
.feature-card.right.bottom { --rotate: 2deg; }

/* ===== HOW IT WORKS SECTION ===== */
.features-overview {
  position: relative;
  padding: 6rem 0;
  background-color: white;
  overflow: hidden;
}

.features-overview::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.08), transparent);
}

.section-title {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 3rem;
}

.section-title h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--dark);
  position: relative;
  display: inline-block;
}

.section-title h2::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  border-radius: 2px;
}

.section-title p {
  font-size: 1.125rem;
  color: var(--gray);
  max-width: 600px;
  margin: 0 auto;
}

.how-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.how-card {
  background: white;
  padding: 2.5rem 1.5rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  text-align: center;
  transition: var(--transition-slow);
  border: 1px solid rgba(0, 0, 0, 0.03);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.how-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  opacity: 0;
  transition: var(--transition);
}

.how-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
  border-color: rgba(59, 130, 246, 0.1);
}

.how-card:hover::before {
  opacity: 1;
}

.how-card .step {
  width: 56px;
  height: 56px;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  color: white;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  font-weight: 700;
  margin: 0 auto 1.5rem;
  box-shadow: 0 10px 20px rgba(37, 99, 235, 0.2);
  position: relative;
  z-index: 1;
}

.how-card .step::after {
  content: '';
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border: 2px solid rgba(59, 130, 246, 0.3);
  border-radius: 50%;
  z-index: -1;
  opacity: 0;
  transition: var(--transition);
}

.how-card:hover .step::after {
  opacity: 1;
  top: -6px;
  left: -6px;
  right: -6px;
  bottom: -6px;
}

.how-card h4 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: var(--dark);
  position: relative;
  padding-bottom: 0.75rem;
}

.how-card h4::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 2px;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  transition: var(--transition);
}

.how-card:hover h4::after {
  width: 80px;
}

.how-card p {
  color: var(--gray);
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-bottom: 0;
}

/* ===== WHY CHOOSE SECTION ===== */
.why-choose {
  position: relative;
  padding: 6rem 0;
  background: linear-gradient(135deg, #f8fafc 0%, #f0f9ff 100%);
  overflow: hidden;
}

.why-choose::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.08), transparent);
}

.why-choose .choose-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.choose-item {
  background: white;
  padding: 2.5rem 2rem;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-md);
  text-align: left;
  transition: var(--transition-slow);
  border: 1px solid rgba(0, 0, 0, 0.03);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.choose-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 0;
  background: linear-gradient(to bottom, var(--primary), var(--accent));
  transition: var(--transition-slow);
  z-index: 1;
}

.choose-item:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(59, 130, 246, 0.1);
}

.choose-item:hover::before {
  height: 100%;
}

.choose-item h4 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: var(--dark);
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.choose-item h4::before {
  content: 'âœ“';
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: rgba(37, 99, 235, 0.1);
  border-radius: 10px;
  font-size: 1rem;
  color: var(--primary);
  flex-shrink: 0;
  transition: var(--transition);
}

.choose-item:hover h4::before {
  background: var(--primary);
  color: white;
  transform: scale(1.1) rotate(5deg);
}

.choose-item p {
  color: var(--gray);
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-left: calc(36px + 0.75rem);
  margin-bottom: 0;
  transition: var(--transition);
}

.choose-item:hover p {
  color: var(--dark-2);
}

/* ===== ABOUT SECTION ===== */
.about-preview {
  position: relative;
  padding: 6rem 0;
  background-color: white;
  overflow: hidden;
}

.about-preview::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.08), transparent);
}

.about-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
}

.about-left {
  max-width: 600px;
}

.about-left h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.about-left h2 {
  font-size: 2.5rem;
  line-height: 1.2;
  margin-bottom: 1.5rem;
  color: var(--dark);
  position: relative;
  display: inline-block;
}

.about-left h2::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  border-radius: 2px;
}

.about-left p {
  font-size: 1.0625rem;
  line-height: 1.7;
  color: var(--gray);
  margin-bottom: 2rem;
}

.about-right {
  position: relative;
  min-height: 400px;
  border-radius: var(--radius-xl);
  overflow: hidden;
  box-shadow: var(--shadow-lg);
  background: linear-gradient(145deg, #f8fafc, #f0f9ff);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.8);
}

