  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg width="100" height="100" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg"><path d="M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z" fill="%233b82f6" fill-opacity="0.1" fill-rule="evenodd"/></svg>');
  opacity: 0.5;
  z-index: 0;
}

.about-right img {
  position: relative;
  z-index: 1;
  max-width: 80%;
  height: auto;
  border-radius: var(--radius-lg);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

/* ===== FAQ SECTION ===== */
.faq-section {
  position: relative;
  padding: 6rem 0;
  background: linear-gradient(135deg, #f8fafc 0%, #f0f9ff 100%);
  overflow: hidden;
}

.faq-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.08), transparent);
}

.faq-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 3rem;
}

.faq-item {
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  overflow: hidden;
  transition: var(--transition);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.faq-item.open {
  box-shadow: var(--shadow-lg);
}

.faq-question {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: transparent;
  border: none;
  text-align: left;
  cursor: pointer;
  transition: var(--transition);
}

.faq-item.open .faq-question {
  background: linear-gradient(90deg, rgba(37, 99, 235, 0.05), transparent);
}

.faq-question h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--dark);
  margin: 0;
  transition: var(--transition);
}

.faq-item.open .faq-question h4 {
  color: var(--primary);
}

.faq-toggle {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(37, 99, 235, 0.1);
  color: var(--primary);
  border-radius: 50%;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1;
  flex-shrink: 0;
  margin-left: 1rem;
  transition: var(--transition);
}

.faq-item.open .faq-toggle {
  background: var(--primary);
  color: white;
  transform: rotate(45deg);
}

.faq-answer {
  overflow: hidden;
}

.faq-answer p {
  padding: 0 1.5rem 1.5rem;
  margin: 0;
  color: var(--gray);
  line-height: 1.6;
  font-size: 0.9375rem;
}

.faq-answer a {
  color: var(--primary);
  font-weight: 500;
  text-decoration: none;
  transition: var(--transition);
}

.faq-answer a:hover {
  text-decoration: underline;
}

/* ===== NEWSLETTER SECTION ===== */
.newsletter-section {
  position: relative;
  padding: 6rem 0;
  background: linear-gradient(135deg, #1e3a8a 0%, #1e40af 100%);
  color: white;
  overflow: hidden;
}

.newsletter-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg width="100" height="100" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg"><path d="M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z" fill="%23ffffff" fill-opacity="0.05" fill-rule="evenodd"/></svg>');
  opacity: 0.3;
  z-index: 0;
}

.newsletter-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: var(--radius-xl);
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 1;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  max-width: 900px;
  margin: 0 auto;
}

.newsletter-left {
  margin-bottom: 2rem;
}

.newsletter-left h3 {
  font-size: 2rem;
  color: white;
  margin-bottom: 1rem;
  font-weight: 700;
}

.newsletter-left p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.125rem;
  margin-bottom: 0;
}

.newsletter-form {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  display: flex;
  gap: 1rem;
  width: 100%;
}

.newsletter-input {
  flex: 1;
  padding: 1rem 1.5rem;
  border-radius: var(--radius-md);
  border: 2px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 1rem;
  transition: var(--transition);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.newsletter-input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.newsletter-input:focus {
  outline: none;
  border-color: white;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.1);
}

.newsletter-section .primary-btn {
  background: white;
  color: var(--primary);
  font-weight: 600;
  padding: 1rem 2rem;
  border-radius: var(--radius-md);
  transition: var(--transition);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.newsletter-section .primary-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  background: white;
  color: var(--primary);
}

.newsletter-section .primary-btn:active:not(:disabled) {
  transform: translateY(0);
}

.privacy-note {
  margin-top: 1.5rem;
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.7);
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.newsletter-message {
  margin-top: 1rem;
  padding: 0.75rem 1rem;
  border-radius: var(--radius-md);
  font-size: 0.9375rem;
  font-weight: 500;
  text-align: center;
  animation: slideIn 0.3s ease-out;
}

.newsletter-message.success {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.3);
}

.newsletter-message.error {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== RESPONSIVE STYLES ===== */
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
  
  .hero-left .hero-title {
    font-size: 2.75rem;
  }
  
  .hero-grid {
    gap: 3rem;
  }
  
  .feature-card {
    width: 200px;
    padding: 1rem;
  }
  
  .feature-card.left.top { left: -1.5rem; top: 2rem; }
  .feature-card.left.bottom { left: -2rem; bottom: 2rem; }
  .feature-card.right.top { right: -1.5rem; top: 3rem; }
  .feature-card.right.bottom { right: -2rem; bottom: 2rem; }
}

@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
  
  .section {
    padding: 5rem 0;
  }
  
  .hero-left .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-left .hero-sub {
    font-size: 1.125rem;
  }
  
  .hero-stats {
    gap: 1.5rem;
  }
  
  .hero-stats .stat strong {
    font-size: 1.5rem;
  }
  
  .how-grid,
  .why-choose .choose-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .faq-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .about-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .about-right {
    min-height: 350px;
    order: -1;
  }
  
  .feature-card {
    display: none;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 1.5rem;
  }
  
  .hero-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .hero-left {
    margin: 0 auto;
  }
  
  .hero-left .hero-sub {
    margin-left: auto;
    margin-right: auto;
  }
  
  .hero-ctas {
    justify-content: center;
  }
  
  .hero-stats {
    justify-content: center;
  }
  
  .phone-stage {
    height: 500px;
    margin-top: 2rem;
  }
  
  .phone-float {
    width: 240px;
    height: 500px;
  }
  
  .how-grid,
  .why-choose .choose-grid,
  .faq-grid {
    grid-template-columns: 1fr;
  }
  
  .newsletter-card {
    padding: 2rem 1.5rem;
  }
  
  .form-group {
    flex-direction: column;
    gap: 1rem;
  }
  
  .newsletter-input {
    width: 100%;
  }
  
  .newsletter-section .primary-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .hero-left .hero-title {
    font-size: 2rem;
  }
  
  .hero-left .hero-sub {
    font-size: 1rem;
  }
  
  .hero-ctas {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .btn {
    width: 100%;
    padding: 0.75rem 1rem;
  }
  
  .hero-stats {
    flex-direction: column;
    gap: 1.5rem;
    text-align: center;
    align-items: center;
  }
  
  .hero-stats .stat {
    align-items: center;
    padding-left: 0;
  }
  
  .hero-stats .stat:not(:last-child)::after {
    display: none;
  }
  
  .phone-stage {
    height: 400px;
  }
  
  .phone-float {
    width: 200px;
    height: 400px;
  }
  
  .section-title h2 {
    font-size: 1.75rem;
  }
  
  .section-title p {
    font-size: 1rem;
  }
  
  .faq-question h4 {
    font-size: 1rem;
  }
  
  .newsletter-card {
    padding: 1.5rem 1rem;
  }
  
  .newsletter-left h3 {
    font-size: 1.5rem;
  }
  
  .newsletter-left p {
    font-size: 1rem;
  }
}
